import * as actionUtilTypes from "./actions"

export type IUtilState = {
  counter: number
}

export const initialUtilState: IUtilState = {
  counter: 0,
};

export default function utilReducer(
  state: IUtilState = initialUtilState,
  action: GeneralAction,
): IUtilState {
  switch (action.type) {
    case actionUtilTypes.INCREMENT_COUNTER:
      return {
        ...state,
        counter: state.counter + 1,
      }
  };
  return state;
}
