import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import actions from "../../../store/actions"
import selectors from "../../../store/selectors"
import Binnacle from "../../organisms/Binnacle";

interface IProps {
  getBinnacle: any;
  responseGetBinnacle: any;
}

interface IState {
}

class ReportsContainer extends Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
    };
  }

  componentDidMount(): void {
    const { getBinnacle } = this.props;
    getBinnacle();
  }

  render() {
    const { responseGetBinnacle } = this.props;
    return (
      <>
        {
          responseGetBinnacle && responseGetBinnacle.success &&
          <Binnacle responseGetBinnacle={responseGetBinnacle?.data} />
        }
      </>
    );
  }
}

const mapStateToProps = (state: any) => ({
  responseGetBinnacle: selectors.user.getResponseGetBinnacle(state.user),
});

function mapDispatchToProps(dispatch: any) {
  return bindActionCreators(
    {
      getBinnacle: actions.user.geBinnacleAction,
    },
    dispatch,
  );
}

const ReportsContainerComponent: any = connect(
  mapStateToProps,
  mapDispatchToProps,
)(ReportsContainer as any);
export default ReportsContainerComponent;

