const getCurrentCommit = async (octo: any, org: any, repo: any, branch: any) => {
    const { data: refData } = await octo.rest.git.getRef({
      owner: org,
      repo,
      ref: `heads/${branch}`,
    })
    const commitSha = refData.object.sha
    const { data: commitData } = await octo.rest.git.getCommit({
      owner: org,
      repo,
      commit_sha: commitSha,
    })
    return {
      commitSha,
      treeSha: commitData.tree.sha,
    }
  }
  const createNewTree = async (octo: any, owner: any, repo: any, blobs: any, paths: any, parentTreeSha: any) => {
    // My custom config. Could be taken as parameters
    const tree = blobs.map(({ sha }: any, index: any) => ({
      path: paths[index],
      mode: `100644`,
      type: `blob`,
      sha,
    }))
    const { data } = await octo.rest.git.createTree({
      owner,
      repo,
      tree,
      base_tree: parentTreeSha,
    })
    return data
  }

  const b64toBlob = (
    b64Data: string,
    contentType: string,
    sliceSize: any,
    filename: string,
  ) => {
    contentType = contentType || "";
    sliceSize = sliceSize || 512;

    const byteCharacters = window.atob(b64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);

      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }

    console.log(byteArrays);

    return new File(byteArrays, filename, { type: contentType });
  }

  const createBlobForFile = async (octo: any, org: any, repo: any, type: any) => {
    var data = localStorage.getItem(type);
    console.debug(type, ", ", data);
    const blob = await octo.request('POST /repos/mrroin/registers/git/blobs', {
      owner: 'mrroin',
      repo: 'registers',
      content: data? data : "[]",
      encoding: 'utf-8',
    })
    return blob.data
  }
  const createNewCommit = async (octo: any, org: any, repo: any, message: any, currentTreeSha: any, currentCommitSha: any) =>
    (await octo.rest.git.createCommit({
      owner: org,
      repo,
      message,
      tree: currentTreeSha,
      parents: [currentCommitSha],
    })).data

  const setBranchToCommit = (octo: any, org: any, repo: any, branch: any, commitSha: any) =>
    octo.rest.git.updateRef({
      owner: org,
      repo,
      ref: `heads/${branch}`,
      sha: commitSha,
  })
