    static checkerUser = (user: any) => {
        console.debug("checkerUser");
        const pusblish = new Promise((resolve, reject) => {
            try{
                const usersString = localStorage.getItem("users");
                let users: any = JSON.parse(usersString? usersString : "[]");
                const existUser = users.filter((item: any) => item.id === user.id);
                if(existUser.length === 0){
                    resolve({
                        success: false,
                        uuid: uuidv4(),
                        data: {
                            error: "Identificador no encontrado",
                        },
                    })
                    return;
                } else {
                    if(existUser[0].nip !== user.nip){
                        resolve({
                            success: false,
                            uuid: uuidv4(),
                            data: {
                                error: "Password incorrecto",
                            },
                        });
                        return;
                    }
                }
                let now: any = new Date();
                let day = String(now.getDate());
                let month = String(now.getMonth() + 1);
                let year = String(now.getFullYear());
                const binnacleString = localStorage.getItem(day+month+year);
                let lastBinnacle: any = JSON.parse(binnacleString? binnacleString : '{"index":0,"records":[]}');
                let endBinnacle: any = { index: 0, records: [] };
                const binnacleAllString = localStorage.getItem("binnacle");
                let binnacleAll: any = JSON.parse(binnacleAllString? binnacleAllString : "[]");
                if(binnacleAll.length > 0){
                    endBinnacle = binnacleAll.reduce((prev: any, current: any) => (prev.index > current.index) ? prev : current);
                }
                const restBinnacle = binnacleAll.filter((item: any) => item.index !== lastBinnacle.index);
                if(!lastBinnacle.created){
                    lastBinnacle.created = now.getTime();
                    lastBinnacle.id = day+month+year;
                }
                const checkedUser = lastBinnacle.records.filter((item: any) => item.id === user.id);
                let lastRecord: any = { index: 0 };
                if(checkedUser.length > 0 && checkedUser[0].records && checkedUser[0].records.length > 0){
                    lastRecord = checkedUser[0].records.reduce((prev: any, current: any) => (prev.index > current.index) ? prev : current);
                }
                if(lastRecord.type === "start"){
                    resolve({
                        success: false,
                        uuid: uuidv4(),
                        data: {
                            error: checkedUser[0].name + " ya registraste entrada el da de hoy, para registrar entrada de nuevo, debe marcar una salida",
                        },
                    });
                    return;
                } else {
                    const now = new Date();
                    const recordsTmp = checkedUser[0]? checkedUser[0].records : [];
                    const recordsWhitout = lastBinnacle.records.filter((item: any) => item.id !== existUser[0].id);
                    const newBinnacle = {
                        ...lastBinnacle,
                        lastUpdate: now.getTime(),
                        dateString: now.toLocaleDateString(undefined, optionsDate),
                        index: endBinnacle.index + 1,
                        records: [
                            ...recordsWhitout,
                            {
                                ...existUser[0],
                                records: [
                                    ...recordsTmp,
                                    {
                                        index: lastRecord.index + 1,
                                        date: now.getTime(),
                                        dateString: now.toLocaleDateString(undefined, optionsDate),
                                        type: "start",
                                    }
                                ],
                            },
                        ]
                    }
                    localStorage.setItem(day+month+year, JSON.stringify(newBinnacle));
                    localStorage.setItem("binnacle", JSON.stringify([...restBinnacle, newBinnacle]));
                    resolve({
                        success: true,
                        data: existUser[0],
                        uuid: uuidv4(),
                    });

                }
            } catch(e: any){
                resolve({
                    success: true,
                    uuid: uuidv4(),
                    data: {
                        error: e.toString(),
                    },
                })
            }
        });
        return from(pusblish).pipe(
            map((response: any) => {
                return response;
            }),
            catchError((error) => {
              // console.error(error);
              return of({
                success: false,
                data: {
                  error: error.toString(),
                },
              }).pipe((x) => x);
            }),
        );
    };
