export const ADD_COUNTER = "ADD_COUNTER"
export const INCREMENT_COUNTER = "INCREMENT_COUNTER"

export function addCounterAction(input: number) {
  return {
    type: ADD_COUNTER,
    payload: { index: input },
  };
}

export function incrementCounterAction() {
  return {
    type: INCREMENT_COUNTER,
    payload: 0,
  };
}
