import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import actions from "../../../store/actions"
import Checker from "../../organisms/Checker"
import selectors from "../../../store/selectors"

interface IProps {
  responseCheckerUser: any;
  checkerUser: any;
  cleanCheckerUser: any;
}

interface IState {
}

class AddUserContainer extends Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
    };
  }

  timer  = () => {
    // Haciendo uso del objeto Date() obtenemos la hora, minuto y segundo 
    let fechahora: any = new Date();
    let hora: any = fechahora.getHours(); 
    let minuto: any = fechahora.getMinutes(); 
    // let segundo: any = fechahora.getSeconds(); 
    let meridiano: any = "AM";
    if(hora === 0){
      hora = 12;
    }
    if(hora > 12) {
        hora = hora - 12;
        meridiano = "PM";
    }
    hora = (hora < 10) ? "0" + hora : hora;
    minuto = (minuto < 10) ? "0" + minuto : minuto;
    // segundo = (segundo < 10) ? "0" + segundo : segundo;
    var tiempo = hora + ":" + minuto + " " + meridiano;
    // var tiempo = hora + ":" + minuto + ":" + segundo + " " + meridiano;
    const inputTag = document.getElementById('timer') as HTMLInputElement;
    inputTag.innerText = tiempo;
    inputTag.textContent = tiempo;
    setTimeout(()=> this.timer(), 60000);
  }

  componentDidMount(): void {
    // this.timer();
  }

  render() {
    const { checkerUser, responseCheckerUser, cleanCheckerUser } = this.props;
    return (
      <>
        <div id="timer" style={{
          color: "#dce9ff",
          fontSize: "60px",
          fontFamily: "Arial",
          letterSpacing: "5px",
          textAlign: "center",
          backgroundColor: "#1976d2",
          border: "10px solid #2167ac",
          borderRadius: "20px",
          marginBottom: "14vh",
        }}>
        </div>
        <Checker title="Registra tu entrada" message="Bienvenido" checkerUser={checkerUser} responseCheckerUser={responseCheckerUser} cleanCheckerUser={cleanCheckerUser} />
      </>
    );
  }
}

const mapStateToProps = (state: any) => ({
  responseCheckerUser: selectors.user.getResponseCheckerUser(state.user),
});

function mapDispatchToProps(dispatch: any) {
  return bindActionCreators(
    {
      checkerUser: actions.user.checkerUserAction,
      cleanCheckerUser: actions.user.responseCheckerUserAction,
    },
    dispatch,
  );
}

const AddUserContainerComponent: any = connect(
  mapStateToProps,
  mapDispatchToProps,
)(AddUserContainer as any);
export default AddUserContainerComponent;

