import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import actions from "../../../store/actions"
import selectors from "../../../store/selectors"
import Users from "../../organisms/Users";

interface IProps {
  getUser: any;
  responseGetUser: any;
  responseUpdateUser: any;
  updateUser: any;
}

interface IState {
}

class UsersContainer extends Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
    };
  }

  componentDidMount(): void {
    const { getUser } = this.props;
    getUser();
  }

  render() {
    const { responseGetUser, updateUser, responseUpdateUser } = this.props;
    return (
      <>
        {
          responseGetUser && responseGetUser.success &&
          <Users responseGetUser={responseGetUser?.data} updateUser={updateUser} responseUpdateUser={responseUpdateUser} />
        }
      </>
    );
  }
}

const mapStateToProps = (state: any) => ({
  responseGetUser: selectors.user.getResponseGetUser(state.user),
  responseUpdateUser: selectors.user.getResponseUpdateUserSelector(state.user),
});

function mapDispatchToProps(dispatch: any) {
  return bindActionCreators(
    {
      getUser: actions.user.getUserAction,
      updateUser: actions.user.updateUserAction,
    },
    dispatch,
  );
}

const UsersContainerComponent: any = connect(
  mapStateToProps,
  mapDispatchToProps,
)(UsersContainer as any);
export default UsersContainerComponent;

