import * as React from "react"
import { useSelector, shallowEqual, useDispatch } from "react-redux"
import { AddCounter } from "../../organisms/AddCounter"
import actions from "../../../store/actions"
import selectors from "../../../store/selectors"
import { Dispatch } from "redux"
import { Link } from "react-router-dom"

const AddCounterContainer: React.FC = () => {
  const counter = useSelector(
    (state: any) => {
      console.debug(state);
      return selectors.util.getCounterSelector(state.util);
    },
    shallowEqual
  )

  const dispatch: Dispatch<any> = useDispatch()

  const addCounter = React.useCallback(
    () => dispatch(actions.util.addCounterAction(1)),
    [dispatch]
  )

  return (
    <main>
      <AddCounter addCounter={addCounter} />
      <h1>Counter</h1>
      {counter}
      <br></br>
      <Link to="/other">Ir a otra pagina</Link>
    </main>
  )
}

export default AddCounterContainer;
