import { useForm, Controller } from "react-hook-form";
import {
  Box,
  Button,
  Grid,
  TextField,
} from "@mui/material";
import { useEffect, useState } from "react";
import ActionAlerts from "../ActionAlerts"

const myHelper: any = {
  id: {
    required: "Required",
  },
};

export default function App({ checkerUser, responseCheckerUser, cleanCheckerUser, message, title }: any) {
  const [error, setError] = useState<any>();
  const [name, setName] = useState<any>();
  const { control, handleSubmit, reset } = useForm({
    reValidateMode: "onBlur"
  });
  console.debug("responseCheckerUser... ", responseCheckerUser);

  const handleOnSubmit = (evt: any) => {
    console.debug(evt);
    checkerUser(evt)
  };

  useEffect(() => {
    console.debug("responseCheckerUser, ", responseCheckerUser);
    if(responseCheckerUser !== undefined && responseCheckerUser !== null){
      cleanCheckerUser();
      if(responseCheckerUser.success){
        console.log("mostrar el ");
        setName(responseCheckerUser.data.name);
        reset();
      } else {
        setError(responseCheckerUser.data.error);
      }
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [responseCheckerUser]);
  return (
    <div className="App">
      <h1>{title}</h1>
      <Box component="form" onSubmit={handleSubmit(handleOnSubmit)}>
        <Grid container spacing={3}>
          <Grid item xs={12} key="k1">
            <Controller
              control={control}
              name="id"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  type="text"
                  inputProps={{
                    form: {
                      autocomplete: "off",
                    },
                  }}
                  fullWidth
                  label="Identificador de usuario"
                  error={error !== undefined}
                  helperText={error ? myHelper.id[error.type] : ""}
                />
              )}
            />
          </Grid>
          <Grid item xs={12} key="k2">
            <Controller
              control={control}
              name="nip"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  type="password"
                  autoComplete="new-password"
                  inputProps={{
                    form: {
                      autocomplete: "off",
                    },
                  }}
                  fullWidth
                  label="Password"
                  error={error !== undefined}
                  helperText={error ? myHelper.id[error.type] : ""}
                />
              )}
            />
          </Grid>
          <Grid item xs={12} key="k8">
            <Button type="submit" variant="contained">Registrar</Button>
          </Grid>
        </Grid>
      </Box>
      {/* {
        error && <DialogError error={error} closeModal={() => setError(false)} />
      } */}
      {
        error && <ActionAlerts name={error} closeAlert={() => setError(undefined)} type="error" message={message} />
      }
      {
        name && <ActionAlerts name={name} closeAlert={() => setName(undefined)} type="success" message={message} />
      }
    </div>
  );
}
