import * as React from "react"
import { useSelector, shallowEqual, useDispatch } from "react-redux"
import actions from "../../../store/actions"
import selectors from "../../../store/selectors"
import { Dispatch } from "redux"
import AddUser from "../../organisms/AddUser"

const AddUserContainer: React.FC = () => {
  const responseUser = useSelector(
    (state: any) => {
      console.debug(state);
      return selectors.user.getResponseUserSelector(state.user);
    },
    shallowEqual
  )
  console.debug("responseUser, ", responseUser);
  const dispatch: Dispatch<any> = useDispatch()

  const addUser = React.useCallback(
    (user: any) => dispatch(actions.user.addUserAction(user)),
    [dispatch]
  )

  const cleanUser = React.useCallback(
    (user: any) => dispatch(actions.user.responseAddUserAction(undefined)),
    [dispatch]
  )
  return (
      <AddUser addUser={addUser} responseUser={responseUser} cleanUser={cleanUser} />
  )
}

export default AddUserContainer;
