import { combineEpics, } from "redux-observable";
import { map, catchError, mergeMap  } from "rxjs/operators";
import { ofType,  } from "redux-observable";
import UserService from "../../service/UserService";

import * as actionUserTypes from "./actions"

const addUserEpic = (
  action$: any,
  state$: any,
) =>
  action$.pipe(
    ofType(actionUserTypes.ADD_USER),
    map((action: GeneralAction) => {
      return action.payload.user;
    }),
    mergeMap((user) =>
      UserService.addUser(user).pipe(
        map((response) => {
          return response;
        }),
      ),
    ),
    mergeMap((response) => {
      return [actionUserTypes.responseAddUserAction(response)];
    }),
    catchError((err) => {
      return [actionUserTypes.responseAddUserAction({
        success: false,
        data: {
          error: err.toString(),
        }
      })];
    }),
  );


