import * as React from 'react';
import { useEffect, useState } from "react";
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import EditIcon from '@mui/icons-material/Edit';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import {
  Button,
  Grid,
  TextField
} from "@mui/material";

const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function Users({ responseGetUser, updateUser, responseUpdateUser }: any) {
  const [open, setOpen] = React.useState(false);
  const [selected, setSelected] = React.useState<any>({});
  const [password, setPassword] = React.useState<any>(undefined);
  const [errorUpdate, setErrorUpdate] = useState<any>();
  const [responseUpdate, setResponseUpdate] = useState<any>("k");
  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    setOpen(false);
    setResponseUpdate(undefined);
    setErrorUpdate(undefined);
  };
  const changePasswordModal = (data: any) => {
    handleOpen();
    setSelected(data);
    console.debug(data);
  };
  const changePassword = () => {
    updateUser({
      ...selected,
      nip: password,
    });
  };


  useEffect(() => {
    console.debug("responseUpdateUser, ", responseUpdateUser);
    if (responseUpdateUser !== undefined && responseUpdateUser !== null) {
      if (responseUpdateUser.success) {
        setResponseUpdate("Usuario actualizado");
      } else {
        setErrorUpdate(responseUpdateUser.data.error);
      }
    }
  }, [responseUpdateUser]);
  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>Nombre</TableCell>
              <TableCell>Correo electrnico</TableCell>
              <TableCell>Telfono</TableCell>
              <TableCell>Cambiar password</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {responseGetUser.map((row: any) => (
              <TableRow
                key={row.name}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                  {row.id}
                </TableCell>
                <TableCell>{row.name + " " + row.middleName + " " + row.lastName}</TableCell>
                <TableCell>{row.mail}</TableCell>
                <TableCell>{row.phone}</TableCell>
                <TableCell><EditIcon onClick={() => changePasswordModal(row)} /></TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <>
          <Box sx={style}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              Cambiar password
            </Typography>
            <>
              {
                errorUpdate || responseUpdate ?
                  <><Typography id="modal-modal-description" sx={{ mt: 2 }}>
                    {errorUpdate || responseUpdate}
                  </Typography>
                    <br />
                    <Button onClick={() => handleClose()} variant={"outlined"}>Cerrar</Button></>
                  :
                  <>

                    <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                      {selected?.name + " ingresa tu nuevo password"}
                    </Typography>
                    <br />
                    <Grid item xs={12} key="k1">
                      <TextField
                        type="text"
                        fullWidth
                        label="Password"
                        error={password === undefined || password === "" || password === null}
                        helperText={password === undefined || password === "" || password === null ? "Requerido" : ""}
                        onChange={(e) => {
                          // console.debug("e", e.target.value);
                          setPassword(e.target.value);
                        }}
                      />
                    </Grid>
                    <br />
                    <Button onClick={() => changePassword()} variant={"outlined"}>Guardar</Button>
                  </>
              }
            </>
          </Box>
        </>
      </Modal>
    </>
  );
}