import { combineEpics, } from "redux-observable";
import { map, catchError,  } from "rxjs/operators";
import {ofType} from "redux-observable";

import * as actionUtilTypes from "./actions"

const addCounterEpic = (
  action$: any,
  state$: any,
) =>
  action$.pipe(
    ofType(actionUtilTypes.ADD_COUNTER),
    map((action: GeneralAction) => {
      console.debug(action.payload);
      return action.payload;
    }),
    map((data) => {
      return actionUtilTypes.incrementCounterAction();
    }),
    catchError((e) => {
      return [actionUtilTypes.incrementCounterAction()];
    }),
  );

export default combineEpics(
  addCounterEpic,
);
