import * as actionUserTypes from "./actions"

export type IUserState = {
  responseAddUser: any
  responseCheckerUser: any;
  responseCheckerOutUser: any;
  responseGetUser: any;
  responseGetBinnacle: any;
  responseUpdateUser: any;
  responseSyncBinnacle: any;
}

export const initialUserState: IUserState = {
  responseAddUser: null,
  responseCheckerUser: null,
  responseCheckerOutUser: null,
  responseGetUser: null,
  responseGetBinnacle: null,
  responseUpdateUser: null,
  responseSyncBinnacle: null,
};

export default function userReducer(
  state: IUserState = initialUserState,
  action: GeneralAction,
): IUserState {
  switch (action.type) {
    case actionUserTypes.RESPONSE_ADD_USER:
      return {
        ...state,
        responseAddUser: action.payload.responseAddUser,
      }
    case actionUserTypes.RESPONSE_CHECKER_USER:
      return {
        ...state,
        responseCheckerUser:
          action.payload.responseCheckerUser,
      }
    case actionUserTypes.RESPONSE_CHECKER_OUT_USER:
      return {
        ...state,
        responseCheckerOutUser:
          action.payload.responseCheckerOutUser,
      }
    case actionUserTypes.RESPONSE_GET_USERS:
      return {
        ...state,
        responseGetUser:
          action.payload.responseGetUser,
      }
    case actionUserTypes.RESPONSE_GET_BINNACLE:
      return {
        ...state,
        responseGetBinnacle:
          action.payload.responseGetBinnacle,
      }
    case actionUserTypes.RESPONSE_UPDATE_USER:
      return {
        ...state,
        responseUpdateUser:
          action.payload.responseUpdateUser,
      }
    case actionUserTypes.RESPONSE_SYNC_BINNACLE:
      return {
        ...state,
        responseSyncBinnacle:
          action.payload.responseSyncBinnacle,
      }
  };
  return state;
}
