import { useForm, Controller } from "react-hook-form";
import {
  Box,
  Button,
  Grid,
  TextField
} from "@mui/material";
import MuiPhoneInput from 'material-ui-phone-number';
import { useEffect, useState } from "react";
import ActionAlerts from "../ActionAlerts"

const myHelper: any = {
  name: {
    required: "Nombre(s) is Required",
  },
  middleName: {
    required: "Apellido Paterno is Required",
  },
  lastName: {
    required: "Apellido Materno is Required",
  },
  birthday: {
    required: "Fecha de Nacimiento is Required",
  },
  address: {
    required: "Direccin is Required",
  },
  email: {
    pattern: "Invalid Email Address"
  },
  phone: {
    required: "Telfono is Required"
  }
};

export default function App({ addUser, cleanUser, responseUser  }: any) {
  const [error, setError] = useState<any>();
  const [name, setName] = useState<any>();
  const { control, handleSubmit, reset } = useForm({
    reValidateMode: "onBlur"
  });

  const handleOnSubmit = (evt: any) => {
    console.debug(evt);
    addUser(evt)
  };

  useEffect(() => {
    console.debug("responseUser, ", responseUser);
    if(responseUser !== undefined && responseUser !== null){
      cleanUser();
      if(responseUser.success){
        console.log("mostrar el ");
        setName("Registro exitoso");
        reset();
      } else {
        setError(responseUser.data.error);
      }
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [responseUser]);

  return (
    <div className="App">
      <Box component="form" onSubmit={handleSubmit(handleOnSubmit)}>
        <Grid container spacing={3}>
          <Grid item xs={12} key="k1">
            <Controller
              control={control}
              name="nip"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  type="text"
                  fullWidth
                  label="Password"
                  error={error !== undefined}
                  helperText={error ? myHelper.name[error.type] : ""}
                />
              )}
            />
          </Grid>
          <Grid item xs={12} key="k1">
            <Controller
              control={control}
              name="name"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  type="text"
                  fullWidth
                  label="Nombre(s)"
                  error={error !== undefined}
                  helperText={error ? myHelper.name[error.type] : ""}
                />
              )}
            />
          </Grid>
          <Grid item xs={12} key="k2">
            <Controller
              control={control}
              name="middleName"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  type="text"
                  fullWidth
                  label="Appellido Paterno"
                  error={error !== undefined}
                  helperText={error ? myHelper.middleName[error.type] : ""}
                />
              )}
            />
          </Grid>
          <Grid item xs={12} key="k3">
            <Controller
              control={control}
              name="lastName"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  type="text"
                  fullWidth
                  label="Apellido Materno"
                  error={error !== undefined}
                  helperText={error ? myHelper.lastName[error.type] : ""}
                />
              )}
            />
          </Grid>
          <Grid item xs={12}  key="k4">
            <Controller
              control={control}
              name="birthday"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  fullWidth
                  label="Fecha de Nacimiento"
                  type="date"
                  defaultValue="2017-05-24"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  error={error !== undefined}
                  helperText={error ? myHelper.birthday[error.type] : ""}
                />
              )}
            />
          </Grid>
          <Grid item xs={12}  key="k5">
            <Controller
              control={control}
              name="address"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  type="text"
                  fullWidth
                  label="Direccin Completa"
                  error={error !== undefined}
                  helperText={error ? myHelper.address[error.type] : ""}
                  multiline
                  rows={4}
                />
              )}
            />
          </Grid>
          <Grid item xs={12} key="k6">
            <Controller
              control={control}
              name="mail"
              defaultValue=""
              rules={{
                pattern: /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/
              }}
              render={({ field, fieldState: { error } }) => (
                <TextField
                  {...field}
                  type="text"
                  fullWidth
                  label="Correo electrnico"
                  error={error !== undefined}
                  helperText={error ? myHelper.email[error.type] : ""}
                />
              )}
            />
          </Grid>
          <Grid item xs={12} key="k7">
            <Controller
              control={control}
              name="phone"
              defaultValue=""
              rules={{
                required: true,
              }}
              render={({ field: { onChange }, fieldState: { error } }) => (
                <>
                  <MuiPhoneInput
                    fullWidth
                    defaultCountry={'mx'}
                    onChange={onChange}
                    onlyCountries={['mx']}
                  />
                  <div style={{
                    color: "#d32f2f",
                    textAlign: "left",
                    fontWeight: 400,
                    fontSize: "0.75rem",
                    lineHeight: 1.66,
                    letterSpacing: "0.03333em",
                    marginTop: 3,
                    marginRight: 14,
                    marginBottom: 0,
                    marginLeft: 14, 
                  }}
                  >{error ? myHelper.phone[error.type] : ""}</div>
                </>
              )}
            />
          </Grid>
          <Grid item xs={12} key="k8">
            <Button type="submit" variant="contained">Guardar</Button>
            <Button onClick={() => reset()} variant={"outlined"}>Limpiar</Button>
          </Grid>
        </Grid>
        {
          error && <ActionAlerts name="" closeAlert={() => setError(undefined)} type="error" message={error} />
        }
        {
          name && <ActionAlerts name="" closeAlert={() => setName(undefined)} type="success" message={name} />
        }
      </Box>
    </div>
  );
}
